.ProfileCard {
  
  position: relative;
  width: 100%;
  height: 100vh;
  font-family: 'Martel', serif;
  overflow: hidden;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  height: 400px;
}

@media (max-width: 768px) {
  .swiper-slide {
    width: 250px;
    height: auto;
  }
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
     filter: grayscale(100%);
     transition: 0.5s ease-in-out;

}

.swiper-slide img:hover{
    filter: grayscale(0);
    transition: 0.5s ease-in-out;
    
}

.container {
  position: relative;
  width: 100%;
  height: 60vh;
  background-color: rgb(209, 51, 51);
  display: block;
  margin-top: 7%;
}

.container .swiper-container {
  width: 70%;
  position: absolute;
  left: 50%;
  bottom: -16vw;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 2;
  padding-bottom: 3vw;
}

@media (max-width: 768px) {
  .container .swiper-container {
    bottom: -58vw;
    padding-bottom: 8vw;
  }
}


.swiper-pagination-fraction {
  bottom: 0;
}

.swiper-pagination-bullet {
  width: 25px;
  height: 5px;
  border-radius: 5px;
}



.profile-content >h1{
  text-align: center;
  margin-top: 3%;
  padding: 10px;
  font-size: 3.5rem;
  color: crimson;
}

.profile-content >p{
  text-align: center;
  flex-wrap: wrap;
  width: 60%;
  margin: auto;
  margin-bottom: 20px;
  padding: 15px;
  font-size: 1.6rem;
  line-height: 2.8rem;
  color: rgb(197, 46, 46);
}

.profilebtn{
  display: flex;
  justify-content: center;
  margin-top: 3.8rem;
    padding: .9rem 3rem;
    font-size: 1.7rem;
    color: #fff;
    position: relative;
    width: 20%;
    margin: auto;
    background: crimson;
    cursor: pointer;
}

.profilebtn:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.24);
    -webkit-transition: none;
       -moz-transition: none;
            transition: none;
}

.profilebtn:hover:after {
  width: 120%;
  background-color: rgba(255,255,255,0);
  
    -webkit-transition: all 0.4s ease-in-out;
       -moz-transition: all 0.4s ease-in-out;
            transition: all 0.4s ease-in-out;
}






@media screen and (max-device-width: 480px)
and (orientation: portrait){
  .profile-content >h1{
    font-size: 2.5rem;
  }
  
  .profile-content >p{
    margin: auto;
    width: 100%;
    font-size: 1rem;
    line-height: 1.8rem;
   
  }
  
  .profilebtn{
      font-size: 1.1rem;
      position: relative;
      width: 60%;
  }
}



@media screen and (min-device-width: 768px)
and (max-device-width: 1024px) {
 
  .profile-content >h1{
    font-size: 2.8rem;
  }
  
  .profile-content >p{
    margin: auto;
    width: 90%;
    font-size: 1.5rem;
    line-height: 2.5rem;
   
  }
  
  .profilebtn{
      font-size: 1.5rem;
      position: relative;
      width: 40%;
  }

}

